export function initTheme() {
  const toggleBtn = document.getElementById("theme-toggle");
  const icon = document.getElementById("theme-icon");
  const body = document.body;

  function setTheme(isLight) {
    if (isLight) {
      body.classList.add("light-mode");
      body.classList.remove("dark");
      // Use Feather Sun Icon SVG
      if (icon) icon.innerHTML = '<svg class="sun-icon" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><circle cx="12" cy="12" r="5"></circle><line x1="12" y1="1" x2="12" y2="3"></line><line x1="12" y1="21" x2="12" y2="23"></line><line x1="4.22" y1="4.22" x2="5.64" y2="5.64"></line><line x1="18.36" y1="18.36" x2="19.78" y2="19.78"></line><line x1="1" y1="12" x2="3" y2="12"></line><line x1="21" y1="12" x2="23" y2="12"></line><line x1="4.22" y1="19.78" x2="5.64" y2="18.36"></line><line x1="18.36" y1="5.64" x2="19.78" y2="4.22"></line></svg>';
    } else {
      body.classList.remove("light-mode");
      body.classList.add("dark");
      // Use Feather Moon Icon SVG
      if (icon) icon.innerHTML = '<svg class="moon-icon" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path></svg>';
    }
  }

  // Always start in light mode (no persistence)
  setTheme(true);

  let rotation = 0;
  if (toggleBtn) {
    toggleBtn.addEventListener("click", () => {
      rotation += 360;
      if (icon) icon.style.transform = `rotate(${rotation}deg)`;
      
      const isLight = !body.classList.contains("light-mode");
      setTheme(isLight);
      localStorage.setItem('theme', isLight ? 'light' : 'dark');
    });
  }
}
